import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';
//全局注册
Vue.use(Vuex);

//创建Store对象
const store = new Vuex.Store({
//状态数据
state:{
    //主题分类数据
    topics:[
        {name:'all',desc:'全部'},
        {name:'good',desc:'精华'},
        {name:'share',desc:'分享'},
        {name:'ask',desc:'问答'},
        {name:'job',desc:'招聘'},
        {name:'dev',desc:'测试'},
      ],
      //未登录时的更多菜单
      unLoginedmoreMenus:{
        login:'登录'
      },
      //登录时的更多菜单
      loginedmoreMenus:{
        create:'发表新话题',
        center:'个人中心',
        msg:'消息通知',
        logout:'登出',
      },
      //登录状态
      isLogined:false,
      //登录的用户名
      loginnname:'',
      //用户登录的accesstoKen
      accesstoken:'',
      //登录用户id
      author_id:'',
      //是否显示更多菜单
      isShowMoreMenus:false
},
//状态数据的计算属性  理解为把属性进行计算 明白了计算属性就是把对象属性进行了计算，输出计算后的对象属性值什么的 <——为什么要用计算属性
getters:{//如果用户已经是登录状态了如何实现右侧菜单。用计算属性完成<———根据用户的登录状态来显示右侧菜单，如果用户未登录状态那右侧菜单显示登录菜单项列表，如果用户是登录状态那右侧菜单显示个人中心、消息通知登出的菜单项列表
    moreMenus:function(state){
        if(state.isLogined){//true
          //用户登录状态，返回登录后的更多菜单
          return state.loginedmoreMenus;
        }
        return state.unLoginedmoreMenus;
      },
      getTopicDescByTab:function(state){
        // 需要返回一个函数结构
        return function(tab){
          for (var index = 0;index<state.topics.length;index++){
            if(state.topics[index].name == tab){
              return state.topics[index].desc;
            }
          }
        }
      }
},
mutations,//同步修改
actions//异步修改
});
//对外输出store模块
export default store;


