
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {mapState} from 'vuex';
import {mapGetters} from 'vuex';
import {mapMutations} from 'vuex';
import VmBackTop from 'vue-multiple-back-top';
export default {
name: 'app',
data:function(){
return {
badgeText:''
}
},
components:{
ButtonTab,
ButtonTabItem,
XHeader,
Actionsheet,
VmBackTop,
Badge
},

created:function(){

var loginStatus = localStorage.getItem('loginStatus');

if(loginStatus){

this.mutationLogined();
this.mutationLoginname(localStorage.getItem('loginname'));
this.mutationAccessToken(localStorage.getItem('accesstoken'));
this.mutationsAuthorId(localStorage.getItem('author_id'));
if(this.isLogined){//当前用户处于登录状态就执行ajax

this.getUnreadMsgCount();

var app = this;
window.setInterval(function(){
app.getUnreadMsgCount();
},
5*60*1000);
}
}
},
computed:{//用计算属性来判断是否是登录状态还是未登录状态,登录的话就显示登录数据，未登录就显示未登录数据
...mapGetters(['moreMenus']),//获取状态数据的计算属性 自己的理解：在vuex中...
...mapState(['topics', 'unLoginedmoreMenus', 'loginedmoreMenus', 'isLogined']),//获取状态数据
isShowMoreMenus:{
get:function(){
return this.$store.state.isShowMoreMenus;

},
set:function(nval){

this.$store.commit('mutationIsShowMoreMenus',nval);
}
}
},

methods:{

getUnreadMsgCount:function(){
var app = this;
this.$http.get('/message/count',{params:{accesstoken:this.$store.state.accesstoken}}).then(function(response){
if(response.data.success){

app.badgeText = response.data.data;
}
});
},
...mapMutations(['mutationLogined','mutationLoginname','mutationAccessToken','mutationsAuthorId']),

routetohome:function(){
this.$router.replace('/');
},

showMoreMenus(){


this.isShowMoreMenus = true;
},

handleClickMoreMenu(menukey,menuitem){


this.$router.push('/'+ menukey);
}
}
}



