
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mapMutations} from 'vuex';
export default {
name: "login",
components: {
XInput,
XButton,
Group,
Toast
},
data: function() {
return {
accesstoken: "",
isShowToast:false,
toastText:''
};
},
methods: {
...mapMutations(['mutationLogined','mutationLoginname','mutationAccessToken','mutationsAuthorId']),

dologin: function() {
var app=this;

this.$http
.post("/accesstoken", { accesstoken: this.accesstoken })
.then(function(response) {//请求成功的后的响应




if(response && response.data && response.data.success){



app.mutationLogined();
app.mutationLoginname(response.data.loginname);
app.mutationAccessToken(app.accesstoken);
app.mutationsAuthorId(response.data.id);



localStorage.setItem('loginStatus',true);
localStorage.setItem('loginname',response.data.loginname);
localStorage.setItem('accesstoken',app.accesstoken);
localStorage.setItem('author_id',response.data.id);

app.$router.push('/user/'+ response.data.loginname);
}

}).catch(function(error){



app.isShowToast = true;
app.toastText = error.response.data.error_msg;

});
}
}
};



