
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import WechatEmotion from 'vux/src/components/wechat-emotion/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState } from "vuex";
export default {
name: "topic",
props: ["id"],
computed: {
...mapState(["isLogined"])
},
components: {
Group,
CellBox,
Flexbox,
FlexboxItem,
WechatEmotion,
XButton,
XTextarea,
Popup
},
directives:{
TransferDom
},
data: function() {
return {
topic: null,
replyContent: '',
isShowPopup:false,
reply_id:null,
reply_loginname:''
};
},
methods: {

decollect:function(){
var app=this;
this.$http.post('/topic_collect/de_collect',{accesstoken:this.$store.state.accesstoken,topic_id:this.topic.id}).then(function(response){

if(response.data&&response.data.success){

app.topic.is_collect=false;
}
});
},

collect:function(){
var app=this;
this.$http.post('/topic_collect/collect',{accesstoken:this.$store.state.accesstoken,topic_id:this.topic.id}).then(function(response){

if(response.data&&response.data.success){

app.topic.is_collect=true;
}
});
},

upOrDown:function(){


var app = this;
this.$http.post('/reply/' + reply_id + '/ups',{accesstoken:this.$store.state.accesstoken}).then(function(rsponse){

if(response && response.data && response.data.success){

var type = response.data.action;
if(type == 'up'){// 点赞

var reply;

for(var index = 0;index <=app.topic.replies.length;index++){
if(app.topic.replies[index].id == reply.id) {

reply = app.topic.replies[index];
break;
}
}




reply.ups.push(app.$store.state.accesstoken);
}else if(type == 'down'){// 取消点赞
var reply;

for(var index = 0;index <=app.topic.replies.length;index++){
if(app.topic.replies[index].id == reply.id) {

reply = app.topic.replies[index];
break;
}
}


reply.ups.pop();
}
}
});
},

hidepopup(){
this.reply_id=null;
},

showPopup:function(reply_id,reply_loginname){


this.isShowPopup = true;
this.reply_id = reply_id;
this.reply_loginname = reply_loginname;

},

addreply: function(replyTab) {


var params={
accesstoken:this.$store.accesstoken,
content:this.replyContent,
};
if(replyTab==2){
params.reply_id=this.reply_id;

params.content = '@'+this.reply_loginname+ '' +params.content;
}

var app = this;
this.$http.post("/topic/" + this.topic.id + "/replies",params)
.then(function(response) {

if (response.data && response.data.success) {


var reply = {
id: response.data.reply_id,
author: {
loginname: app.$store.loginname,

avatar_url: ""
},
content: params.content,
ups: [],
create_at: "2014-10-07T12:11:18.981Z",
reply_id: null,
is_uped: false
};
if(replyTab == 2){
reply.reply_id = app.reply_id;
}
app.topic.replies.push(reply);

app.replyContent='';

app.reply_id=null;
app.idShowPopup=false;
app.reply_loginname='';
}
});
}
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {


vm.$http.get("/topic/" + to.params.id,{params:{accesstoken:vm.$store.state.accesstoken}}).then(function(response) {

vm.topic = response.data.data;
});
});
},

beforeRouteUpdate: function(to, from, next) {
var app = this;
this.$http.get("/topic/" + to.params.id,{params:{accesstoken:this.$store.state.accesstoken}}).then(function(response) {

this.topic = response.data.data;
});
next();
}
};



