
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import axios from "axios";
export default {
components: {
XTextarea,
Group,
XButton,
mavonEditor
},
data: function() {
return {
title: "",
content: "",
toolbars: {
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线
ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
fullscreen: true, // 全屏编辑
htmlcode: true, // 展示html源码
help: true, // 帮助
/* 1.3.5 */
save: true, // 保存（触发events中的save事件）
/* 1.4.2 */
navigation: true, // 导航目录
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true 
}
};
},
methods: {

uploadImage: function(pos, file) {
var app = this;


console.log(pos);
console.log(file);

var params =new FormData();
params.append('smfile',file);

axios.post("https://sm.ms/api/upload", params,{



headers: { 'Content-Type': 'multipart/form-data' }
}).then(function(response) {


app.$refs.md.$img2Url(pos,response.data.data.url);
});
},

addtopic: function() {


var app = this;
this.$http("/topics", {
title: this.title,
content: this.content,
tab: "dev",
accesstoken: this.$store.state.acccesstoken
}).then(function(response) {

if (response && response.data.success) {

app.$router.replace("/topiclist/dev");
}
});
}
}
};



